@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2");
  src: url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
  src: url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal; }

* {
  box-sizing: border-box; }

body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  color: #ffffff;
  box-sizing: border-box; }

ul {
  list-style: none;
  padding: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

a {
  color: #ffffff;
  text-decoration: none; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.header {
  min-height: 877px;
  background: #1b2d37 url("../img/bg-header.jpg") center no-repeat;
  background-size: cover;
  overflow: hidden;
  z-index: 0; }
  @media (max-width: 1023px) {
    .header {
      min-height: 684px; } }
  @media (max-width: 767px) {
    .header {
      min-height: 650px; } }

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header {
    background: #1b2d37 url("../img/bg-header@2x.jpg") center no-repeat;
    background-size: cover; } }

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px; }
  @media (max-width: 1023px) {
    .header__container {
      padding-top: 15px; } }
  @media (max-width: 767px) {
    .header__container {
      padding-top: 6px; } }

.header__logo {
  margin-left: 18px; }
  @media (max-width: 1023px) {
    .header__logo {
      margin-left: 25px; } }
  @media (max-width: 767px) {
    .header__logo {
      margin-left: 15px; } }

.header__list {
  display: flex;
  align-items: center; }

.header__email {
  margin-right: 80px; }
  .header__email:hover {
    opacity: 0.7; }
  @media (max-width: 1023px) {
    .header__email {
      margin-right: 34px; } }
  @media (max-width: 767px) {
    .header__email {
      margin-right: 23px; } }

.header__phone {
  margin-right: 50px; }
  .header__phone:hover {
    opacity: 0.7; }
  @media (max-width: 1023px) {
    .header__phone {
      margin-right: 23px; } }
  @media (max-width: 767px) {
    .header__phone {
      margin-right: 15px; } }

.header__email-img {
  width: 19px;
  height: 14px; }
  @media (max-width: 1023px) {
    .header__email-img {
      width: 25px;
      height: 18px; } }

.header__phone-img {
  width: 18px;
  height: 18px; }
  @media (max-width: 1023px) {
    .header__phone-img {
      width: 24px;
      height: 24px; } }

.header__email-text,
.header__phone-text {
  font-family: "PT Mono", "Courier New", monospace;
  margin-left: 20px; }
  @media (max-width: 1023px) {
    .header__email-text,
    .header__phone-text {
      display: none; } }

.header__call {
  display: inline-block;
  border: 1px solid #00b2ff;
  background-color: #00b2ff;
  border-radius: 5px;
  text-align: center;
  padding: 15px 0;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.05em;
  cursor: pointer;
  width: 180px;
  font-size: 14px;
  line-height: 16px;
  margin-right: 5px;
  padding: 11px 0;
  border: 1px solid #ffffff;
  background-color: transparent; }
  .header__call:hover {
    background-color: #00b2ff;
    border: 1px solid #00b2ff; }
  @media (max-width: 1023px) {
    .header__call {
      display: none; } }

.logo {
  display: flex; }
  .logo:hover {
    opacity: 0.7; }

.logo__img {
  width: 52px;
  height: 52px;
  margin-right: 16px; }
  @media (max-width: 767px) {
    .logo__img {
      width: 38px;
      height: 38px; } }

.logo__description {
  display: flex;
  flex-direction: column;
  margin-top: 5px; }
  @media (max-width: 767px) {
    .logo__description {
      display: none; } }

.logo__title {
  margin: 0;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff; }

.logo__text {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
  opacity: 0.3; }
  @media (max-width: 1023px) {
    .logo__text {
      opacity: 1; } }

.intro {
  position: relative;
  width: 720px;
  margin-top: 220px;
  margin-left: 18px;
  z-index: 1; }
  @media (max-width: 1023px) {
    .intro {
      width: 470px;
      margin-top: 120px;
      margin-left: 88px; } }
  @media (max-width: 767px) {
    .intro {
      width: 290px;
      margin: 0 auto;
      margin-top: 225px; } }

.intro__title {
  font-weight: bold;
  font-size: 80px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin: 0; }
  @media (max-width: 1023px) {
    .intro__title {
      font-size: 60px; } }
  @media (max-width: 767px) {
    .intro__title {
      position: relative;
      font-size: 40px;
      z-index: 1; } }

.intro__text {
  position: relative;
  padding-left: 98px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 22px;
  line-height: 34px;
  margin-top: 30px; }
  .intro__text::after {
    content: "";
    position: absolute;
    top: 15px;
    left: 0;
    width: 66px;
    height: 2px;
    background-color: #ffffff; }
  @media (max-width: 1023px) {
    .intro__text {
      padding-left: 51px;
      font-size: 18px;
      margin-top: 22px; }
      .intro__text::after {
        content: "";
        position: absolute;
        top: 23px;
        left: 3px;
        width: 33px;
        height: 2px;
        background-color: #ffffff; } }
  @media (max-width: 767px) {
    .intro__text {
      position: relative;
      padding-left: 51px;
      font-size: 16px;
      margin-top: 25px;
      line-height: 26px; }
      .intro__text::after {
        top: 18px; } }

.intro__btn {
