      display: none; } }

.intro__btn-mobile {
  display: none; }
  @media (max-width: 767px) {
    .intro__btn-mobile {
      display: inline-block;
      border: 1px solid #00b2ff;
      background-color: #00b2ff;
      border-radius: 5px;
      text-align: center;
      padding: 15px 0;
      text-transform: uppercase;
      font-weight: bold;
      letter-spacing: 0.05em;
      cursor: pointer;
      width: 287px;
      margin-left: 3px;
      margin-top: 26px; } }

.intro__img {
  position: absolute;
  top: -57px;
  left: 786px;
  width: 705px;
  height: 515px; }
  .intro__img::before {
    content: "";
    position: absolute;
